<template>
  <div>
    <header>
       <!--  -->
       <span class="return" onclick="window.history.go(-1)">&lt;</span>
       <span>{{wRtitle}}</span>
       <!-- loading -->
       <span v-show="!wRtitle">loading...</span>
     </header>
    <div class="wR" v-show="!loading">
      <ul>
        <!--  -->
        <li v-for="item in wRlist">
          <router-link :to="/ReleaseAll/+item.id">
            <!--  -->
            <img class="fl" :src="item.images.small" />
            <!--  -->
            <h6 >{{item.title}}</h6>
            <div><strong>:</strong><span v-for="name in item.casts">{{name.name}},</span></div>
            <div><strong>:</strong><span v-for="plot in item.genres">{{plot}},</span></div>           
          </router-link>
        </li>
      </ul>
    </div>
    <!--  -->
    <div class="total" v-show="!loading,wRtitle"><b></b>{{totalALL}}&nbsp;&nbsp;<b></b>{{page+1}}/{{total}}</div>
    <!--  -->
    <div class="paging" v-show="!loading">
      <a class="Previous" @click="Previous">&lt;&nbsp;</a>
      <a class="Next" @click="next">&nbsp;&gt;</a>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex'
let start = 0;
export default {
  data () {
    return {
      wRlist: [],
      wRtitle: '',
      totalALL: Number,
      total: Number,
      page: ''
    }
  },
  computed:mapGetters([
    'loading' // 
  ]),
  mounted () {
    // 
    start = 0;
    this.http(start);
  },
  methods: {
    http (start) {
      // console.log(start)
      var _this = this;
      // 
      _this.$store.dispatch('showLoading');
      // 
      // https://bird.ioliu.cn/v1?url=http://api.douban.com/v2/movie/coming_soon?start='+start+'&count=10
      this.$http.get('https://bird.ioliu.cn/v1?url=http://api.douban.com/v2/movie/top250?start='+start+'&count=10')
        .then(function (response) {
          // 
          _this.wRlist = response.data.subjects;
          // 
          _this.wRtitle = response.data.title;
          // 
          _this.totalALL = response.data.total;
          _this.total = Math.ceil(response.data.total/10);
          // 
          _this.page = start/10;
          //  
          _this.$store.dispatch('hideLoading');
        })
        .catch(function (error) {
          console.log(error);
        });

    },
    Previous () {
      // 
      if(start <= 0) {
        return false;
      } else {
        start-=10;
      }
      // 
      this.http(start);
    },
    next() {
      //   
      if((start+10)/10 >= this.total) {
         return false;
      } else {
        start+=10;
      };
      // 
      this.http(start);
    }
  }

}
</script>



